









import Vue from 'vue';
const Share = require('../util/nb189Share.js');
export default Vue.extend({
  name: 'home',
  data() {
    return {
      tData: this.$store.state.tData,
      $util: Vue.prototype.$util,
    }
  },
  watch: {
    '$store.state.tData': function(v) {
      this.tData = v;
    }
  },
  methods: {
    startTest: function() {
      if(this.tData.home.btn.point.enforce === true) { //为true时候会有埋点
        const setData = this.$util.setPoint(this.tData.home.btn.point.set);
        if(setData.length === 3) { //传入三个值才会设置
          (<any>window)._uxt.push(['_trackEvent', setData[0], setData[1], setData[2]]); 
        } else {
          (<any>window)._uxt.push(['_trackEvent', `${this.tData.title}_${(<any>window).channel}_首页`, '点击', this.tData.home.btn.text||'开始测试']); 
        }
      }
      this.$router.push('/behavior');
    }
  },
  mounted: function() { 
    document.title = this.tData.home.title || this.tData.title;
    if(this.tData.home.share) {
      Share.default.initWeixin(this.tData.home.shaer);
    }
  }
})
