






import Vue from 'vue';
import axios from 'axios';

export default Vue.extend({
  data() {
    return {
      tData: this.$store.state.tData,
      $util: Vue.prototype.$util,
    }
  },
  watch: {
    '$store.state.tData': function(v) {
      this.tData = v;
      this.$store.commit('setAppBg', this.tData.home.bg.url);
      this.$store.commit('setAppBgStyle', this.tData.home.bg.style);
    },
    '$store.state.tData.home.bg.url': function(v) {
      this.$store.commit('setAppBg', this.tData.home.bg.url);
    },
    '$store.state.tData.home.bg.style': function(v) {
      this.$store.commit('setAppBgStyle', this.tData.home.bg.style);
    },
  },
  methods: {
    insertStyle: function(){ //动态引入样式，主要引用字体
      let style = document.createElement('style'), fonts = ``;
      Object.keys(this.tData.fonts).forEach((v, i) => {
        fonts += `@font-face { 
          font-family: ${v};
          src: url(${this.tData.fonts[v]});
        }`;
      });
      style.type = 'text/css';
      style.innerHTML = fonts;
      (<any>window).document.getElementsByTagName('head').item(0).appendChild(style);
    },
    getUserInfo() { //获取微信用户头像昵称
      if(sessionStorage.getItem('_userInfo')) {
        this.$store.commit('userInfo', JSON.parse(sessionStorage.getItem('_userInfo')||'')); //store里面保存了
      }
      let href = window.location.href, hrefData: any[], codeString: string = '';
      if(href.indexOf('?') === -1) return;
      hrefData = href.split('?');
      if(href.indexOf('&') != -1) {
        hrefData = hrefData[1].split('&');
      }
      hrefData.forEach((v, i) => {
        let vl = v.split('=');
        if(vl[0] === 'code') {
          codeString = vl[1].replace(/#/g, '').replace(/\//g, '');
          console.log(codeString);
        }
      });
      if(!codeString)  { //刷新不会二次获取头像，只能从本地获取
        return;
      }
      (<any>window)._uxt.push(['_trackEvent', '育儿冷知识问答_授权申请', '点击“允许”', '进入页面', '']); 
      axios.post('https://activity.tech.21cn.com/sign/wx/authorization', { //获取用户信息
        code: codeString
      }).then(v => {
        if(v.data.code != 1) {
          console.log(v);
          return;
        }
        this.$store.commit('userInfo', v.data.data);
      }).catch(v => { //因为二次请求code会无效，这里服务器会响应慢
        console.log(v);
        if(sessionStorage.getItem('_userInfo')) {
          this.$store.commit('userInfo', JSON.parse(sessionStorage.getItem('_userInfo')||'')); //store里面保存了
        }
      });
    },
    UpdateData() { //实时更新数据
    // axios.get('http://127.0.0.1:7001/egg').then(v => {
    //      this.$store.commit('setData', v.data);
    //    });
      if(sessionStorage.getItem('_preview'))
        this.$store.commit('setData', JSON.parse(sessionStorage.getItem('_preview')||''));
    }
  },
  beforeCreate: function() {

  },
  created: function() {
    this.insertStyle();
    if(this.tData.home.bg) {
      this.$store.commit('setAppBg', this.tData.home.bg.url);
      this.$store.commit('setAppBgStyle', this.tData.home.bg.style);
    }
  },
  mounted: function() {
    if(this.tData.home.point.enforce != false) {
      const setData = this.$util.setPoint(this.tData.home.point.set);
      if(setData.length === 3) {
        (<any>window)._uxt.push(['_trackEvent', setData[0], setData[1], setData[2]]); 
      } else {
        (<any>window)._uxt.push(['_trackEvent', `${this.tData.title}_${(<any>window).channel}_首页`, '进入页面', '访问活动页']); 
      }
    }
    this.$store.commit('setTopic', this.tData.behavior.reply.topic); //设置题目
    if((<any>window).location.href.indexOf('code=') === -1) {
      // this.$router.push('/jump');
    } else {
      this.getUserInfo();
    }
    setInterval(() => { //实时更新数据
      this.UpdateData();
    }, 1000);
  }
})
