import axios from 'axios'
import qs from 'qs'
import wx from 'weixin-js-sdk'
let Share = {
  isWeixin: function() {
    var ua = window.navigator.userAgent.toLowerCase()
    return ua.indexOf('micromessenger') >= 0 ? true : false
  },
  isYixin: function() {
    var ua = window.navigator.userAgent.toLowerCase()
    return ua.indexOf('yixin') >= 0 ? true : false
  },
  isAndroid: function() {
    var ua = window.navigator.userAgent.toLowerCase()
    return ua.indexOf('android') >= 0 ? true : false
  },
  isIOS: function() {
    var ua = window.navigator.userAgent.toLowerCase()
    return ua.indexOf('iphone') >= 0 ||
      ua.indexOf('ipad') >= 0 ||
      ua.indexOf('ipod') >= 0
      ? true
      : false
  },
  initWeixin: function(opts) {
    var that = this
    if (that.isWeixin()) {
      axios.post(document.location.protocol + '//nb.189.cn/portal/getWeixinJsSign.do',
        qs.stringify({
          sUrl: window.location.href
        })
        ).then((data) => {
            wx.config({
              debug: false,
              appId: data.appid,
              timestamp: data.timestamp,
              nonceStr: data.noncestr,
              signature: data.sign,
              jsApiList: [
                'onMenuShareTimeline',
                'onMenuShareAppMessage',
                'onMenuShareQQ'
              ]
            })
            wx.ready(function() {
              wx.onMenuShareAppMessage({
                title: opts.title,
                link: opts.url,
                desc: opts.desc,
                imgUrl: opts.imgUrl
              })
              wx.onMenuShareTimeline({
                title: opts.title,
                desc: opts.desc,
                link: opts.url,
                imgUrl: opts.imgUrl
              })
              wx.onMenuShareQQ({
                title: opts.title,
                desc: opts.desc,
                link: opts.url,
                imgUrl: opts.imgUrl
              })
            })
        })
    }
  },
  initYixin: function(opts) {
    var that = this
    if (that.isYixin()) {
      document.addEventListener('YixinJSBridgeReady', function() {
        YixinJSBridge.on('menu:share:appmessage', function() {
          YixinJSBridge.invoke(
            'sendAppMessage',
            {
              img_url: opts.imgUrl,
              img_width: opts.imgWdith,
              img_height: opts.imgHeight,
              link: opts.url,
              title: opts.title,
              desc: opts.desc
            },
            function() {}
          )
        })
        YixinJSBridge.on('menu:share:timeline', function() {
          YixinJSBridge.invoke(
            'shareTimeline',
            {
              img_url: opts.imgUrl,
              img_width: opts.imgWdith,
              img_height: opts.imgHeight,
              link: opts.url,
              title: opts.title,
              desc: opts.desc
            },
            function() {}
          )
        })
      })
    }
  }
}

export default Share;
