import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './util/rem'
import util from "./util/util";
import axios from 'axios';

Vue.config.productionTip = false
Vue.prototype.$img = './';
Vue.use(util);
axios.get('./data.json').then(v => {
  store.commit('setData', v.data);
  setTimeout(() => { //因为发布端数据渲染比较慢，如果这个预览页面加载速度比发布端快，就会报错，故加定时器延迟加载
    new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount('#app')
  }, 2000);
})

