import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    tData: {}, //所有信息
    topic: [], //所有题目
    currentNum: 0, //题目号
    currentTopic: {}, //当前题目
    scoring: 0, //计分
    clockRun: false, //时钟是否运行
    Timing: 80, //时钟默认计时
    masker: false, //是否显示弹框
    appBg: ``, //背景图片
    appBgStyle: ``, //背景图片样式
    userInfo: {
      // nickname: 'store',
      // headimgurl: 'https://thirdwx.qlogo.cn/mmopen/vi_32/Rc3IRgNmX9AOZculfHaJzvZR9QViaJT9ia2GJS4e5DBIqia96icebPJGKncQicObkm1ICbFd5WBjBv1bILibn1ZOliaBw/132'
      nickname: '',
      headimgurl: ''
    }, //用户信息，包括头像昵称等
  },
  mutations: {
    ClockRun: function (state, v) { //
      state.clockRun = v;
    },
    Timing: function (state, v) {
      if (state.Timing < 0) return;
      if (v === 0 || v) {
        state.Timing = v;
      }
    },
    refreshTopic: function (state, v) { //刷新题目
      if (v || v == 0) {
        state.currentNum = v;
      }
      if (state.currentNum >= state.topic.length) return;
      state.currentTopic = state.topic[state.currentNum];
      state.currentNum++;
    },
    answer: function (state, v) { //答题
      if (state.currentNum > state.topic.length) return;
      if (v) state.scoring++;
    },
    masker: function (state, v) { //触发显示隐藏
      if (v === true || v === false) {
        state.masker = v;
      } else {
        state.masker = !state.masker;
      }
    },
    scoring: function (state, v) {
      if (!v && v != 0) return;
      state.scoring = v;
    },
    setTopic: function (state, v) {
      if (!v) return;
      state.topic = v;
    },
    setAppBg: function (state, v) { //设置背景图片
      if (!v) return;
      state.appBg = v;
    },
    setAppBgStyle: function (state, v) { //设置背景图片样式
      if (!v) return;
      state.appBgStyle = v;
    },
    userInfo: function (state, v) { 
      console.log(v)
      if (!v) return;
      v.headimgurl = JSON.stringify(v.headimgurl).replace(/"/g, '');
      if (v.headimgurl.indexOf('https') != 0) {
        v.headimgurl = v.headimgurl.replace(/http:/g, document.location.protocol);
      }
      state.userInfo = v;
      sessionStorage._userInfo = JSON.stringify(v); //保存到本地，万一刷新还可以有头像
    },
    setData: function (state, v) {
      state.tData = v;
    }
  },
  actions: {

  }
})
