import Vue from 'vue';
const util = {
  install() {
    Vue.prototype.$util = {
      setPoint(setData: string[], rule = {answer:'', num: ''}) { //过滤埋点信息，加上渠道号
        if (setData.length != 3) return false;
        let set: string[] = [];
        setData.forEach((v: string, i: number) => {
          set[i] = v.replace(/\$channel/g, (<any>window).channel).replace(/\$num/g, rule.num).replace(/\$answer/g, rule.answer);
        })
        return set;
      },
      evil(str: string) { //代替eval
        let fn = Function;
        str = str.replace(/"/g, '').replace(/'/g, '');
        return (new fn('',`return ${str}`))();
      },
      getUrlData(keys:string) {
        let href = window.location.href,
        hrefData = href.split('?'), data = '';
        if(href.indexOf('&') != -1) {
          hrefData = hrefData[1].split('&');
        }
        hrefData.forEach((v, i) => {
          let vl = v.split('=');
          if(vl[0] === keys) {
            data = vl[1].replace(/#/g, '').replace(/\//g, '');
          }
        });
        return data;
      },
      urlencode(str:string) {  //将字符串转为urlencode
        return encodeURIComponent(str).replace(/!/g, '%21').replace(/'/g, '%27').replace(/\(/g, '%28').  
        replace(/\)/g, '%29').replace(/\*/g, '%2A').replace(/%20/g, '+');  
      } 
    }
  },
}

export default util;